// angular
import { Component, OnDestroy, OnInit, PLATFORM_ID, Inject } from '@angular/core';
import { isPlatformBrowser, isPlatformServer } from '@angular/common';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';
// libs
import * as _ from 'lodash';
import 'rxjs/add/operator/takeUntil';
import { Observable } from 'rxjs/Observable';
import { ReplaySubject } from 'rxjs/ReplaySubject';

// app
import { AppState } from '../../store';
import { routeAnimation } from '../../app.animations';
import { BaseResponse, IUserDetails } from '../../models';
import { IFormListItem, IFormsResponse } from '../../models/form';
import { FormsActions } from '../../actions';

@Component({
	templateUrl: './home.component.html',
	styleUrls: ['home.component.scss'],
	animations: [routeAnimation]
})
export class HomeComponent implements OnInit, OnDestroy {

	public user: IUserDetails;
	public formsList: IFormsResponse[];
	public isFormLoading: Observable<boolean>;

	private destroyed$: ReplaySubject<boolean> = new ReplaySubject(1);

	constructor(
		private store: Store<AppState>,
		private router: Router,
        private _formsActions: FormsActions,
        @Inject(PLATFORM_ID) private platformId: Object
    ) {
      if (isPlatformBrowser(this.platformId)) {
        // list to show hide laoder
        this.isFormLoading = this.store.select(p => p.form.gettingFormListInProgress).takeUntil(this.destroyed$);
      }
		
	}

    public ngOnInit(): void {
      if (isPlatformBrowser(this.platformId)) {
        this.store.select(p => p.form.formList).takeUntil(this.destroyed$).subscribe((data: any[]) => {
          // this.isFormLoading = Observable.of(false);
          if (data && data.length) {
            this.prepareFormListForUI(data);
          } else {
            // empty array case
            this.formsList = [];
          }
        });
      }

	}

	public prepareFormListForUI(data: IFormsResponse[]): void {
		// get ready data alike Tiles view
		this.formsList = _.map(data, (item: IFormsResponse) => {
			item.color = this.getRandomColor();
			item.cols = 1;
			item.rows = 1;
			return item;
		});
	}

	public getRandomColor() {
		// "#"+((1<<24)*Math.random()|0).toString(16)
		var letters = '0123456789ABCDEF';
		var color = '#';
		for (var i = 0; i < 6; i++) {
			color += letters[Math.floor(Math.random() * 16)];
		}
		return color;
	}

	public redirectToForm($event: any, model: IFormsResponse) {
		// imp to prevent redirect
		$event.preventDefault();
		// dispath action
		this.store.dispatch(this._formsActions.setCurrentForm(model));
		// redirect to form layout
		this.router.navigate(['/', 'app', 'form', model.id]);
	}

	public ngOnDestroy() {
		this.destroyed$.next(true);
		this.destroyed$.complete();
	}

	public previewForm(form: IFormsResponse) {
		console.log ('previewForm', form);
	}

	public deleteForm(form: IFormsResponse) {
		console.log ('deleteForm', form);
		this.store.dispatch(this._formsActions.deleteForm(form.id));
	}

	public copyForm(form: IFormsResponse) {
		console.log ('copyForm', form);
	}
}



