import { ChangeDetectorRef, Component, Input, OnChanges, OnInit } from '@angular/core';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';

@Component({
	selector: 'socket-relative-loader',
	templateUrl: './relative.loader.component.html',
	styleUrls: ['./relative.loader.component.scss']
})
export class RelativeLoaderComponent implements OnInit, OnChanges {
	@Input() public status: boolean;
	public showLoader: boolean = false;
	constructor(private cdref: ChangeDetectorRef) {
	}

	public ngOnInit() {
		//
	}

	public ngOnChanges() {
		this.showLoader = this.status;
		this.cdref.markForCheck();
	}
}



